main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}

:root {
  --primary: #add8e6;
  --secondary: #f3a847;
  --tertiary: #b12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background: var(--light);
  color: var(--dark);
  font-family: Arial, Helvetica, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
  margin-left: 7.5%;
  margin-top: 5%;
}

h2 {
  font-size: 2rem;
  margin-left: 5%;
  margin-top: 20px;
}

nav {
  margin-left: 25px;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  font-weight: lighter;
  margin-top: 0;
}

a {
  border-radius: 5px;
  color: var(--primary);
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
  color: var(--secondary);
  opacity: 70%;
}

header {
  background: var(--light);
  padding: 0;
  margin: 0;
  border-bottom: solid 10px;
  position: relative;
  width: 100%;
}

header a {
  color: var(--dark);
  margin-left: 35px;
  margin-top: 35px;
}

.nav-link-active {
  background-color: var(--dark);
  color: var(--light);
  padding: 5px 10px;
  opacity: 90%;
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: 0.8;
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

img {
  max-width: 100%;
  border-radius: 10%;

}

.picture {
  width: 95%;
  margin-left: 5%;
  flex-wrap: wrap;
  border-radius: 10%;
}

.row {
  flex-direction: row;
  display: flex;
}

.about {
  margin-top: 10px;
  margin-left: 40px;

}

.topic {
  margin-left: 7.5%;
}

span[role="img"] {
  cursor: default;
}

#contact-form {
  padding: 10px;
  width: 85%;
  margin: 5% 5% 5%;
}

form {
  width: 60%;
  border: solid 2px;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
  width: 85%;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
  color: var(--secondary);
}

nav li {
  cursor: pointer;
}

textarea {
  width: 90%;
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
  align-items: right;
}

.profile-text {
  margin-left: 35%;
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
  flex-wrap: wrap;
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
  background: rgba(0, 0, 0, 0.65);
  bottom: 0;
  left: 0;
  overflow: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;
}

.modalContainer {
  background: var(--darker);
  border-radius: 5px;
  max-width: 100%;
  margin: 50px auto;
  padding: 15px;
  width: 960px;
}

.modalTitle {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 25px;
}

button {
  background: var(--primary);
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 30px;
  padding: 10px 15px;
  margin-top: 1%;
  margin-left: 5%;
}

input {
  padding: 10px 10px;
  margin: 10px 10px;
  max-width: 50%;
}

a {
  border-radius: 5px;
  color: var(--primary);
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;
}

.navActive {
  color: var(--secondary);
}

footer {
  text-align: center;
  padding: 20px 25px;
}

.logo {
  margin: 0;
  padding: 10px;
  gap: 0;
}

.icon {
  margin-left: 43%;
}

.profile-icon {
  padding: 10px 10px 10px 10px;
  border-style: solid;
  margin-left: 5%;
  margin-right: 5%;
}

.resume-icon {
  padding: 25px 25px;
  margin: 15px 15px;
}

.resume-row {
  margin-left: 10%;
  margin-right: 10%;
}

.profile-row {
  padding: 10px 10px 10px 10px;
  margin-left: 10%;
  margin-right: 10%;
}

.resume-link {
  margin-left: 24%;
  margin-right: 24%;
  text-align: center;
  margin-top: 5%;
  font-size: xx-large;
  border-radius: 10px;
  background-color: black;
}

.resume-list {
  padding: 20px 25px;
  text-align: center;
}


@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) {

  .picture {
    width: 100%;
    border-radius: 25px;

  }

  .about {
    width: 80%;
    margin-left: 20%;
  }
}